"use client"

import Link from "next/link"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { mockJobs, mockAdminMetrics } from "@/lib/mock-data"
import { Plus, Briefcase, Users, TrendingUp, BarChart3 } from "lucide-react"
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from "recharts"

export default function RecruiterDashboard() {
  const stats = [
    { label: "Active Jobs", value: mockJobs.length, icon: Briefcase },
    { label: "Total Applications", value: 127, icon: Users },
    { label: "Avg Match Score", value: "82%", icon: TrendingUp },
    { label: "Positions Filled", value: "12", icon: BarChart3 },
  ]

  return (
    <div className="p-6 space-y-8">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-3xl font-bold text-foreground">Welcome Back!</h2>
          <p className="text-muted-foreground mt-2">Here's your recruitment dashboard</p>
        </div>
        <Link href="/recruiter/jobs/new">
          <Button className="gap-2">
            <Plus className="h-4 w-4" />
            Post New Job
          </Button>
        </Link>
      </div>

      {/* Stats Grid */}
      <div className="grid md:grid-cols-4 gap-4">
        {stats.map((stat) => {
          const Icon = stat.icon
          return (
            <Card key={stat.label}>
              <CardHeader className="flex flex-row items-center justify-between space-y-0">
                <CardTitle className="text-sm font-medium">{stat.label}</CardTitle>
                <Icon className="h-4 w-4 text-primary" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{stat.value}</div>
              </CardContent>
            </Card>
          )
        })}
      </div>

      {/* Quick Actions */}
      <Card>
        <CardHeader>
          <CardTitle>Quick Actions</CardTitle>
          <CardDescription>Manage your recruitment process</CardDescription>
        </CardHeader>
        <CardContent className="grid md:grid-cols-3 gap-4">
          <Link href="/recruiter/jobs/new">
            <Button variant="outline" className="w-full bg-transparent h-20 flex flex-col items-center justify-center">
              <Plus className="h-6 w-6 mb-2" />
              <span>Post New Job</span>
            </Button>
          </Link>
          <Link href="/recruiter/jobs">
            <Button variant="outline" className="w-full bg-transparent h-20 flex flex-col items-center justify-center">
              <Briefcase className="h-6 w-6 mb-2" />
              <span>View Jobs</span>
            </Button>
          </Link>
          <Link href="/recruiter/dashboard">
            <Button variant="outline" className="w-full bg-transparent h-20 flex flex-col items-center justify-center">
              <BarChart3 className="h-6 w-6 mb-2" />
              <span>Analytics</span>
            </Button>
          </Link>
        </CardContent>
      </Card>

      {/* Recent Jobs */}
      <Card>
        <CardHeader>
          <CardTitle>Recent Job Postings</CardTitle>
          <CardDescription>Your latest job openings</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {mockJobs.slice(0, 3).map((job) => (
              <Link key={job.id} href={`/recruiter/jobs/${job.id}`}>
                <div className="flex items-center justify-between p-4 border rounded-lg hover:bg-secondary/50 transition-colors cursor-pointer">
                  <div className="flex-1">
                    <h3 className="font-semibold text-foreground">{job.title}</h3>
                    <p className="text-sm text-muted-foreground">{job.location}</p>
                  </div>
                  <div className="text-right">
                    <Badge variant="secondary">{job.applicationCount} Applications</Badge>
                    <p className="text-sm text-muted-foreground mt-1">{job.matchedCandidates} matches</p>
                  </div>
                </div>
              </Link>
            ))}
          </div>
          <Link href="/recruiter/jobs" className="mt-4">
            <Button variant="outline" className="w-full bg-transparent">
              View All Jobs
            </Button>
          </Link>
        </CardContent>
      </Card>

      {/* Growth Chart */}
      <Card>
        <CardHeader>
          <CardTitle>Platform Growth</CardTitle>
          <CardDescription>Users, jobs, and applications over time</CardDescription>
        </CardHeader>
        <CardContent>
          <ResponsiveContainer width="100%" height={300}>
            <LineChart data={mockAdminMetrics.platformGrowth}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="month" />
              <YAxis />
              <Tooltip />
              <Legend />
              <Line type="monotone" dataKey="users" stroke="#2563eb" strokeWidth={2} />
              <Line type="monotone" dataKey="jobs" stroke="#0891b2" strokeWidth={2} />
              <Line type="monotone" dataKey="applications" stroke="#16a34a" strokeWidth={2} />
            </LineChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
    </div>
  )
}
